import React, { useContext } from 'react';
import Header from './components/Header/Header';
import Dates from './components/Calendar/Dates';

import CalendarContext from './store/calendar-context';
import AddEditEvent from './components/Events/AddEditEvent';
import EventDetails from './components/Events/EventDetails';
import AuthenticationHandler from './components/Authentication/AuthenticationHandler';

function App() {
  const ctx = useContext(CalendarContext);

  return (
    <>
      {ctx.isAddingEvent && <AddEditEvent />}
      {ctx.isEditingEvent && <AddEditEvent />}
      {ctx.isExpandingEvent && <EventDetails />}
      {ctx.isLoggingIn && <AuthenticationHandler />}

      {ctx.isAuthenticated ?
        <> 
          <Header />
          <Dates />
        </> 
        :
        <>
          <Header />
          <h4>Please login to view your calendar!</h4>
        </>
          
      }

    </>
  );
}

export default App;
