import React, { useState } from 'react';

const CalendarContext = React.createContext({

    calendarDates: [],
    setCalendarDates: () => {},
    isAddingEvent: false,
    isEditingEvent: false,
    setIsEditingEvent: () => {},
    isExpandingEvent: false,
    setIsExpandingEvent: () => {},
    todayDate: 0,
    eventDetailProps: {},
    setEventDetailProps: () => {},

    addNewEvent: () => {},
    eventSubmit: () => {},
    hideEvent: () => {},

    titleInput: '',
    titleChange: () => {},
    yearInput: '',
    yearChange: () => {},
    monthInput: '',
    monthChange: () => {},
    dayInput: '',
    dayChange: () => {},
    textInput: '',
    EventTextChange: () => {},
    setTitleInput: () => {},
    setYearInput: () => {},
    setMonthInput: () => {},
    setDayInput: () => {},
    setTextInput: () => {},

    deleteEvent: () => {},
    setTempIdHolder: () => {},

    isLoggingIn: false,
    setIsLoggingIn: () => {},
    isAuthenticated: false,
    setIsAuthenticated: () => {},
    selectedUser: '',
    setSelectedUser: () => {},
});

export const CalendarContextProvider = (props) => {

    const [calendarDates, setCalendarDates] = useState([
        {
            id: "2021-5-30",
            event: null,
            text: ''
        },
        {
            id: "2021-5-31",
            event: null,
            text: ''
        },
        {
            id: "2021-6-1",
            event: null,
            text: ''
        },
        {
            id: "2021-6-2",
            event: null,
            text: ''
        },
        {
            id: "2021-6-3",
            event: null,
            text: ''
        },
        {
            id: "2021-6-4",
            event: null,
            text: ''
        },
        {
            id: "2021-6-5",
            event: null,
            text: ''
        },        {
            id: "2021-6-6",
            event: null,
            text: ''
        },
        {
            id: "2021-6-7",
            event: null,
            text: ''
        },
        {
            id: "2021-6-8",
            event: null,
            text: ''
        },
        {
            id: "2021-6-9",
            event: null,
            text: ''
        },
        {
            id: "2021-6-10",
            event: null,
            text: ''
        },        
        {
            id: "2021-6-11",
            event: null,
            text: ''
        },
        {
            id: "2021-6-12",
            event: null,
            text: ''
        },
        {
            id: "2021-6-13",
            event: null,
            text: ''
        },
        {
            id: "2021-6-14",
            event: null,
            text: ''
        },
        {
            id: "2021-6-15",
            event: null,
            text: ''
        },
        {
            id: "2021-6-16",
            event: null,
            text: ''
        },
        {
            id: "2021-6-17",
            event: null,
            text: ''
        },
        {
            id: "2021-6-18",
            event: null,
            text: ''
        },
        {
            id: "2021-6-19",
            event: null,
            text: ''
        },
        {
            id: "2021-6-20",
            event: null,
            text: ''
        },        
        {
            id: "2021-6-21",
            event: null,
            text: ''
        },
        {
            id: "2021-6-22",
            event: null,
            text: ''
        },
        {
            id: "2021-6-23",
            event: null,
            text: ''
        },
        {
            id: "2021-6-24",
            event: null,
            text: ''
        },
        {
            id: "2021-6-25",
            event: null,
            text: ''
        },        
        {
            id: "2021-6-26",
            event: null,
            text: ''
        },
        {
            id: "2021-6-27",
            event: null,
            text: ''        
        },
        {
            id: "2021-6-28",
            event: null,
            text: ''        
        },
        {
            id: "2021-6-29",
            event: null,
            text: ''
        },
        {
            id: "2021-6-30",
            event: null,
            text: ''
        }
    ]);

    const today = new Date();
    const todayYear = today.getFullYear();
    const todayMonth = today.getMonth()+1;
    const todayDate = today.getDate();
    const [isAddingEvent, setIsAddingEvent] = useState(false);
    const [isEditingEvent, setIsEditingEvent] = useState(false);
    const [isExpandingEvent, setIsExpandingEvent] = useState(false);
    const [eventDetailProps, setEventDetailProps] = useState({});

    const [titleInput, setTitleInput] = useState('');
    const [yearInput, setYearInput] = useState(todayYear);
    const [monthInput, setMonthInput] = useState(todayMonth);
    const [dayInput, setDayInput] = useState(todayDate);
    const [tempIdHolder, setTempIdHolder] = useState('');
    const [textInput, setTextInput] = useState('');
    const [isLoggingIn, setIsLoggingIn] = useState(false);
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [selectedUser, setSelectedUser] = useState('')

    const addNewEventHandler = () => {
        setIsAddingEvent(true)
    };

    const updateEvent = (enteredDate, newDate) => {
        const updatedCalenderDates = calendarDates.map((eachDate)=> {
            if (eachDate.id === tempIdHolder) {
                eachDate.event = null; 
            }
            if (eachDate.id === enteredDate){
                return newDate;
            } else {
                return eachDate;
            }
        })
        setCalendarDates(updatedCalenderDates);
        isAddingEvent ? setIsAddingEvent(false): setIsEditingEvent(false);
        setTitleInput('');
        setYearInput(todayYear);
        setMonthInput(todayMonth);
        setDayInput(todayDate);
        setTextInput('')
    } 

    const throwError = () => {
        alert("EVENT ALREADY EXISTS ON THE DESIRED DATE!!!")
    }

    const eventSubmitHandler = (event) => {
        event.preventDefault();
        const enteredDate = yearInput+'-'+monthInput+'-'+dayInput;
        const newDate =         
        {
            id: enteredDate,
            event: titleInput,
            text: textInput
        }
        const eventExistingCheck = calendarDates.filter((eachDate) => eachDate.id === enteredDate)

        if (isAddingEvent) {
            if (eventExistingCheck[0].event === null) {
                updateEvent(enteredDate, newDate);
            } else {
                throwError();
            }

       } else {
            if (eventExistingCheck[0].event === null || eventExistingCheck[0].id === tempIdHolder) {
                updateEvent(enteredDate, newDate);
            } else {
                throwError();
            }       
         } 
    }
        
    const hideEvent = () => {
        if (isAddingEvent) {
            setIsAddingEvent(false);
        } else if (isEditingEvent) {
            setIsEditingEvent(false);
        } else if (isExpandingEvent){
            setIsExpandingEvent(false) ;
        } else {
            setIsLoggingIn(false);
        }
         return;
    }

    const TitleChangeHandler = (event) =>{
        setTitleInput(event.target.value)
    }    
    const YearChangeHandler = (event) =>{
        setYearInput(event.target.value)
    }
    const MonthChangeHandler = (event) =>{
        setMonthInput(event.target.value)
    }
    const DayChangeHandler = (event) =>{
        setDayInput(event.target.value)
    }
    const EventTextChange = (event) => {
        setTextInput(event.target.value)
    }
    const DeleteEventHandler = (deletingEvent) => {
        const updatedCalenderDates = calendarDates.map((eachDate)=> {
            if (eachDate.id === deletingEvent.id) {
                eachDate.event = null;
            }
            return eachDate;
        }
        )
        setCalendarDates(updatedCalenderDates);
    }
    return (
        <CalendarContext.Provider value ={{
            calendarDates: calendarDates,
            setCalendarDates: setCalendarDates,
            isAddingEvent: isAddingEvent,
            isEditingEvent: isEditingEvent,
            setIsEditingEvent: setIsEditingEvent,
            isExpandingEvent: isExpandingEvent,
            setIsExpandingEvent: setIsExpandingEvent,
            todayDate: todayDate,
            eventDetailProps: eventDetailProps,
            setEventDetailProps: setEventDetailProps,

            addNewEvent: addNewEventHandler,
            eventSubmit: eventSubmitHandler,
            hideEvent: hideEvent,

            titleInput: titleInput,
            titleChange: TitleChangeHandler,
            yearInput: yearInput,
            yearChange: YearChangeHandler,
            monthInput: monthInput,
            monthChange: MonthChangeHandler,
            dayInput: dayInput,
            dayChange: DayChangeHandler,
            setTitleInput: setTitleInput,
            setYearInput: setYearInput,
            setMonthInput: setMonthInput,
            setDayInput: setDayInput,
            setTextInput: setTextInput,
            
            textInput: textInput,
            EventTextChange: EventTextChange,

            deleteEvent: DeleteEventHandler,
            setTempIdHolder: setTempIdHolder,

            isLoggingIn: isLoggingIn,
            setIsLoggingIn: setIsLoggingIn,
            isAuthenticated: isAuthenticated,
            setIsAuthenticated: setIsAuthenticated,
            selectedUser: selectedUser,
            setSelectedUser: setSelectedUser

        }}>
            {props.children}
        </CalendarContext.Provider>
    );

}

export default CalendarContext;