import React, { useContext } from 'react';
import CalendarContext from '../../store/calendar-context';
import css from './Date.module.css';

const Date = (props) => {
    const ctx = useContext(CalendarContext);
    const theYear = props.id.split('').splice(0,4).join('');
    const theMonth = props.id.split('').splice(5).join('')[0]
    const theDate = props.id.split('').splice(7,9).join('');

    const deleteEvent = (event) => {
        event.stopPropagation();
        ctx.deleteEvent(props)
    };

    const editEvent = (event) => {
        event.stopPropagation();
        ctx.setTitleInput(props.event);
        ctx.setYearInput(+theYear);
        ctx.setMonthInput(+theMonth);
        ctx.setDayInput(+theDate);
        ctx.setTextInput(props.text);
        ctx.setTempIdHolder(props.id);
        ctx.setIsEditingEvent(true);


    }
    const expandEvent = () => {
        ctx.setIsExpandingEvent(true);
        ctx.setEventDetailProps(props);
    }
    return (
        <div className={css.date}>

            <p>{theDate}</p>

            {props.event !== null && 
                <div className={css.scheduledEvent} onClick={expandEvent}>
                    <div className={css.title}>
                        {props.event}
                    </div>
                    <div className={css.buttons}>
                            <button className={css.button1} onClick={editEvent} >Edit</button>
                            <button className={css.button2} onClick={deleteEvent}>Delete</button>
                    </div>
                </div>
            }
        </div> 


    );

};

export default Date;