import React, { useContext } from 'react';
import Modal from '../../UI/Modal';
import CalendarContext from '../../store/calendar-context';
import css from './AuthenticationHandler.module.css';

const AuthenticationHandler = () => {
    const ctx = useContext(CalendarContext);
    const authHandler = (event) => {
        event.preventDefault();
        ctx.setIsLoggingIn(false);
        ctx.setIsAuthenticated(true);
    };
    const selectUser = (event) => {
        ctx.setSelectedUser(event.target.value)
    };
    return (
        <Modal>
            <form className={css.auth} onSubmit={authHandler} id="loginForm">
                <label>User:   </label>
                <select id="user" onChange={selectUser}>
                    <option value="John Smith">John Smith</option>
                    <option value="George Washington">George Washington</option>
                    <option value="Bill Gates">Bill Gates</option>
                    <option value="Stephen Curry">Stephen Curry</option>
                </select>
                <button className={css.button} type="submit" value="submit" form="loginForm">Log In</button>
            </form>

        </Modal>
    );
};

export default AuthenticationHandler;