import React, { useContext } from 'react';
import NoteContext from '../../store/calendar-context';
import css from './AddEditEventForm.module.css';

const AddEditEventForm = props => {
    const ctx = useContext(NoteContext);

    return (
        <>
            <form className={css.form} onSubmit={ctx.eventSubmit} id="eventForm">
            <label>Event Title:    </label>
            <input type="text" 
                value={ctx.titleInput} 
                onChange={ctx.titleChange}
            />
            <div>
            <label>Event Date:    </label>
            <input type="number"
                min='2021'
                max='2021'
                step='1'
                value={ctx.yearInput} 
                onChange={ctx.yearChange}
            />
            <input type="number" 
                min='6'
                max='6'
                step='1'
                value={ctx.monthInput} 
                onChange={ctx.monthChange}
            />
            <input type="number" 
                min={ctx.todayDate}
                max='30'
                step='1'
                value={ctx.dayInput} 
                onChange={ctx.dayChange}
            />
            </div>
            
            <br />
            <textarea 
                placeholder={!ctx.editingNote ? 'Enter any note for the event' : null }
                value={ctx.textInput} 
                onChange={ctx.EventTextChange}
            />
            </form>
            <div className={css.buttons}>
                <button className={css.button} onClick={ctx.hideEvent}>Close</button>
                <button className={css.button} type="submit" value="submit" form="eventForm">Add</button>
            </div>

        </>
    );
};
export default AddEditEventForm;