import React, { useContext } from 'react';
import CalendarContext from '../../store/calendar-context';

import css from './Header.module.css';

const Header = () => {

	const ctx = useContext(CalendarContext);
	const buttoncss = `${ctx.isAuthenticated ? css.col2 : css.col3}`;
 
	const LoginHandler = () => {
		ctx.setIsLoggingIn(true);
	};

	const LogoutHandler = () => {
		ctx.setIsAuthenticated(false);
	};

	return (
		<header className={css.header}>
			<div className={css.col1}>
				<h2>Calendar</h2>
			</div>
			<div className={buttoncss}>
				{ctx.isAuthenticated && <button className={css.button} onClick={ctx.addNewEvent}>Add Event</button>}
				{ctx.isAuthenticated && <p>Welcome, {ctx.selectedUser}!!</p>}
				{ctx.isAuthenticated ? 
					<button className={css.button} onClick={LogoutHandler}>Log Out</button> :
					<button className={css.button} onClick={LoginHandler}>Log In</button>}
			</div>

		</header>

	);
};

export default Header;